import React, { useState } from 'react';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import EntityContainer from 'modules/entity/containers';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import { usePagination, useSearchQuery } from 'shared/hooks';
import Pagination from '@material-ui/lab/Pagination';
import IconButton from '@material-ui/core/IconButton';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { RejectModal } from './RejectModal';
import { AcceptIncomingModal } from './AcceptIncomingModal';
import { SwitchBusinessSearch } from './SwitchBusinessSearch';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

export type BusinessRequest = {
	id: number;
	requested_company?: {
		id: number;
		name: string;
		color: string;
	};
	role: string;
};

function IncomingBusinessRequestsInner() {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const { page, handlePage, handlePageChange } = usePagination();
	const { searchQuery, setQuery, query } = useSearchQuery();
	const [rejectModal, setRejectModal] = useState<BusinessRequest | null>(null);
	const [acceptModal, setAcceptModal] = useState<BusinessRequest | null>(null);

	const handleSearch = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
		handlePageChange(1);
		setQuery(e.target.value);
	};

	const handleModals = (item: BusinessRequest | null, type: 'accept' | 'reject') => {
		if (item) {
			if (type === 'accept') setAcceptModal(item);
			if (type === 'reject') setRejectModal(item);
			return;
		}
		setRejectModal(null);
		setAcceptModal(null);
	};

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="incomingRequest"
					name={`AllIncomingRequest-${companyId}`}
					url={`/${companyId}/my/company/user_connection_requests/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const requests = items.map((item: unknown) => item) as BusinessRequest[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '7%' }}>№</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '26%' }}>
													{t('requested_company')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '15%' }}>
													{t('user_role')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '25%' }}>
													{t('actions')}
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '26%' }}>
													<SwitchBusinessSearch
														query={query}
														handleSearch={handleSearch}
														placeholder={t('search_by_business')}
													/>
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{requests.map((item: BusinessRequest, idx: number) => {
												return (
													<StyledTableRow key={item.id}>
														<StyledTableCell component="th" scope="row">
															{`${idx + 1}.`}
														</StyledTableCell>
														<StyledTableCell>{item?.requested_company?.name}</StyledTableCell>
														<StyledTableCell>
															{t(convertToLocalizationKey(item?.role))}
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex">
																<IconButton
																	aria-label="accept"
																	color="primary"
																	onClick={() => handleModals(item, 'accept')}
																>
																	<CheckIcon />
																</IconButton>
																<IconButton
																	aria-label="reject"
																	color="primary"
																	onClick={() => handleModals(item, 'reject')}
																>
																	<CloseIcon />
																</IconButton>
															</div>
														</StyledTableCell>
														<StyledTableCell />
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<AcceptIncomingModal open={acceptModal} handleModalOpen={handleModals} />
								<RejectModal open={rejectModal} handleModalOpen={handleModals} />
								<div className="mt-4 mb-4">
									<Pagination count={meta?.last_page} page={page || 1} onChange={handlePage} />
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<Loader />
			)}
		</div>
	);
}

export default function IncomingBusinessRequests() {
	return (
		<ErrorBoundary>
			<IncomingBusinessRequestsInner />
		</ErrorBoundary>
	);
}
