import React, { useState } from 'react';
import { useAppState } from 'shared/state';
import { WhiteModal, Button, DialogActions, DialogTitle } from 'shared/components/Elements';
import { Loader } from 'shared/components';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { BusinessRequest } from './IncomingBusinessRequests';
import { Trans, useTranslation } from 'react-i18next';

type Props = {
	handleModalOpen: (item: BusinessRequest | null, type: 'accept' | 'reject') => void;
	open: BusinessRequest | null;
};

export function AcceptIncomingModal({ handleModalOpen, open }: Props) {
	const { t } = useTranslation();
	const { companyId } = useAppState();
	const [loading, setLoading] = useState(false);
	const { handleRequest: handleAcceptRequest } = useEntityForm({
		entity: 'incomingRequest',
		name: `AllIncomingRequest-${companyId}`,
		method: 'post',
		url: `/${companyId}/my/company/user_connection_requests/${open?.id}/accept/`,
		deleteData: true,
		id: open?.id,
		cb: {
			success: (data) => {
				handleModalOpen(null, 'accept');
				setLoading(false);
			},
			error: (error) => {
				setLoading(false);
			}
		}
	});

	const acceptRequest = (request: BusinessRequest) => {
		setLoading(true);
		handleAcceptRequest();
	};

	const handleAccept = () => {
		if (open) acceptRequest(open);
	};

	const closeModal = () => {
		handleModalOpen(null, 'reject');
	};

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)}>
				<DialogTitle id="alert-dialog-title">
					<Trans
						i18nKey="accept_business_request_confirmation"
						values={{ businessName: open?.requested_company?.name }}
					/>
				</DialogTitle>
				<DialogActions>
					<Button onClick={closeModal} autoFocus variant="contained">
						{t('cancel')}
					</Button>
					<Button onClick={handleAccept} variant="contained" color="secondary">
						{t('accept')}
					</Button>
				</DialogActions>
				{loading && (
					<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
						<Loader />
					</div>
				)}
			</WhiteModal>
		</div>
	);
}
