import React, { useState } from 'react';
import { useAppState } from 'shared/state';
import {
	WhiteModal,
	Button,
	DialogActions,
	DialogContent,
	DialogContentText,
	DialogTitle
} from 'shared/components/Elements';
import { ErrorBoundary, Loader } from 'shared/components';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { BusinessRequest } from './IncomingBusinessRequests';
import { Trans } from 'react-i18next';
import { t } from 'i18next';

type Props = {
	handleModalOpen: (item: BusinessRequest | null, type: 'accept' | 'reject') => void;
	open: BusinessRequest | null;
};

function RejectModalInner({ handleModalOpen, open }: Props) {
	const { companyId } = useAppState();
	const [loading, setLoading] = useState(false);

	const { handleRequest: handleRejectRequest } = useEntityForm({
		entity: 'incomingRequest',
		name: `AllIncomingRequest-${companyId}`,
		method: 'post',
		url: `/${companyId}/my/company/user_connection_requests/${open?.id}/reject/`,
		deleteData: true,
		id: open?.id,
		cb: {
			success: (data) => {
				handleModalOpen(null, 'reject');
				setLoading(false);
			},
			error: (error) => {
				setLoading(false);
			},
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			finally: () => {}
		}
	});

	const rejectRequest = (request: BusinessRequest) => {
		setLoading(true);
		handleRejectRequest();
	};

	const handleReject = () => {
		if (open) rejectRequest(open);
	};

	const closeModal = () => {
		handleModalOpen(null, 'reject');
	};

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)}>
				<DialogTitle id="alert-dialog-title">
					<Trans
						i18nKey="reject_business_request_confirmation"
						values={{ businessName: open?.requested_company?.name }}
					/>
				</DialogTitle>
				<DialogContent>
					<DialogContentText id="alert-dialog-description">
						{t('you_will_not_accept_again')}
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={closeModal} autoFocus variant="contained">
						{t('cancel')}
					</Button>
					<Button onClick={handleReject} variant="contained" color="secondary">
						{t('reject')}
					</Button>
				</DialogActions>
				{loading && (
					<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
						<Loader />
					</div>
				)}
			</WhiteModal>
		</div>
	);
}

export function RejectModal(props: Props) {
	return (
		<ErrorBoundary>
			<RejectModalInner {...props} />
		</ErrorBoundary>
	);
}
